'use strict';
// 设置 原生导航栏 为一个可下拉选择
// {
// //   selected: '...', //默认选中列表中的位置
// //     items:[{         //列表数据
// //   name:'第一行',
// //   code:'01'
// // },
// //   {
// //     name:'第二行',
// //     code:'02'
// //   }
// // ]
// // }
// {
//   code:0,
//     response:{
//   name:'第一行',
//     code:'01'
// }
// }
import {WinJSDec, fnError, appError} from './APP/config';

var name = 'SET_MENUTITLE';
var SET_MENUTITLE = (obj, fn = _ => {
}) => {
  if (typeof fn === 'function') {
    WinJSDec(_ => {
      window.WinJSBridge.call('webview', 'setMenuTitle', obj, res => {
        var {code, response} = res;
        if (code == 0) {
          fn(response);
        } else {
          throw new Error(`[${name}] ${appError}`);
        }
      });
    });
  } else {
    throw new TypeError(`[${name}] ${fnError}`);
  }
};
export default SET_MENUTITLE;